import React from "react";
import SearchPresenter from "./SearchPresenter";
import { movieApi, tvApi } from "../../api";
import { Redirect } from "react-router";

export default class extends React.Component {
  state = {
    movieResults: null,
    tvResults: null,
    searchTerm: "",
    error: null,
    loading: false,
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const { searchTerm } = this.state;
    if (searchTerm !== "") {
      this.searchByTerm(searchTerm);
      this.props.history.push(`/search?query=${searchTerm}`);
    }
  };
  updateTerm = (event) => {
    const {
      target: { value },
    } = event;
    this.setState({
      searchTerm: value,
    });
  };

  searchByTerm = async (query) => {
    try {
      this.setState({ loading: true });
      let movieResults = null;
      let tvResults = null;
      //   console.log(`query:${query}`);
      // query ê°’ì´ nullì´ë©´ , null ê°’ì„ ë°˜í™˜í•´ì„œ ê²€ìƒ‰ ì´ˆê¸°ìƒíƒœê°€ ë˜ê²Œ ë§Œë“¤ê¸° ìœ„í•œ ë¡œì§
      // movieResults = [] ë¡œ ì´ˆê¸°í™”ë¥¼ í•˜ë©´ presenterì—ì„œ ìˆëŠ”ê±¸ë¡œ ì¸ì‹ì„í•´ì„œ
      // dataë¥¼ ê°€ì ¸ì˜¤ì§€ ëª»í•œê±¸ë¡œ íŒë‹¨í•´ì„œ ë©”ì„¸ì§€ë¥¼ ë„ìš´ë‹¤.
      if (query) {
        ({
          data: { results: movieResults },
        } = await movieApi.search(query));
        ({
          data: { results: tvResults },
        } = await tvApi.search(query));
      }
      this.setState({
        movieResults,
        tvResults,
      });
    } catch {
      this.setState({
        error: "Can't find results",
      });
    } finally {
      this.setState({
        loading: false,
      });
    }
  };
  queryCheck() {
    const {
      location: { search },
    } = this.props;
    const params = new URLSearchParams(search);
    const query = params.get("query");
    //   console.log(`query: ${query}`);
    if (query === null) {
      // í˜„ì¬ queryê°€ ì•„ë¬´ê°’ë„ ì—†ì„ë•Œ ! searchTerm ì´ˆê¸°í™”ì‹œì¼œì£¼ê³  ë¦¬ë Œë”ë§ ê·¸ë¦¬ê³  url ì´ë™!
      this.setState({ searchTerm: "" });
      this.props.history.push("/search");
    } else {
      this.setState({ searchTerm: query });
    }
    this.searchByTerm(query);
  }
  componentDidMount() {
    console.log("hello I am did mount!");
    this.queryCheck();
  }
  componentDidUpdate(prevProps) {
    console.log("hello I am update ~");
    const {
      location: { search: prevSearch },
    } = prevProps;
    const {
      location: { search: nowSearch },
    } = this.props;
    // console.log(prevSearch, nowSearch);

    if (prevSearch !== nowSearch) {
      this.queryCheck();
    }
  }
  render() {
    const { movieResults, tvResults, searchTerm, error, loading } = this.state;
    return (
      <SearchPresenter
        movieResults={movieResults}
        tvResults={tvResults}
        searchTerm={searchTerm}
        error={error}
        loading={loading}
        handleSubmit={this.handleSubmit}
        updateTerm={this.updateTerm}
      />
    );
  }
}
