import React from "react";
import ViewMorePresenter from "./ViewMorePresenter";
import { movieApi, tvApi } from "../../api";

export default class extends React.Component {
  state = {
    results: null,
    loading: true,
    error: null,
    page: 3,
    scroll: false,
  };

  async getData() {
    const {
      match: {
        params: { type, subject, query },
      },
    } = this.props;
    console.log(`type:${type} , subject:${subject}, query:${query}`);

    try {
      if (type === "movie") {
        let movies1 = [];
        let movies2 = [];
        if (subject === "upComing") {
          ({
            data: { results: movies1 },
          } = await movieApi.upComing(1));
          ({
            data: { results: movies2 },
          } = await movieApi.upComing(2));
        } else if (subject === "nowPlaying") {
          ({
            data: { results: movies1 },
          } = await movieApi.nowPlaying(1));
          ({
            data: { results: movies2 },
          } = await movieApi.nowPlaying(2));
        } else if (subject === "popular") {
          ({
            data: { results: movies1 },
          } = await movieApi.popular(1));
          ({
            data: { results: movies2 },
          } = await movieApi.popular(2));
        } else if (subject === "search") {
          ({
            data: { results: movies1 },
          } = await movieApi.search(query, 1));
          ({
            data: { results: movies2 },
          } = await movieApi.search(query, 2));
        }
        const movies = [...movies1, ...movies2];
        this.setState({
          results: movies,
        });
      } else if (type === "show") {
        let shows1 = [];
        let shows2 = [];
        if (subject === "popular") {
          ({
            data: { results: shows1 },
          } = await tvApi.popular(1));
          ({
            data: { results: shows2 },
          } = await tvApi.popular(2));
        } else if (subject === "topRated") {
          ({
            data: { results: shows1 },
          } = await tvApi.topRated(1));
          ({
            data: { results: shows2 },
          } = await tvApi.topRated(2));
        } else if (subject === "airingToday") {
          ({
            data: { results: shows1 },
          } = await tvApi.airingToday(1));
          ({
            data: { results: shows2 },
          } = await tvApi.airingToday(2));
        }
        const shows = [...shows1, ...shows2];
        this.setState({
          results: shows,
        });
      }
    } catch {
      this.setState({ error: "Can not find Data from API" });
    } finally {
      this.setState({ loading: false });
    }
  }
  async getDataMore(page) {
    const {
      match: {
        params: { type, subject, query },
      },
    } = this.props;
    const { results, scroll } = this.state;
    // dconsole.log(`${type} and ${subject} and ${query}`);
    try {
      if (scroll) {
        if (type === "movie") {
          let movies1 = [];

          if (subject === "upComing") {
            ({
              data: { results: movies1 },
            } = await movieApi.upComing(page));
          }
          if (subject === "nowPlaying") {
            ({
              data: { results: movies1 },
            } = await movieApi.nowPlaying(page));
          }
          if (subject === "popular") {
            ({
              data: { results: movies1 },
            } = await movieApi.popular(page));
          }
          if (subject === "search") {
            ({
              data: { results: movies1 },
            } = await movieApi.search(query, page));
          }
          const movies = [...results, ...movies1];
          // console.log(`${type} and ${subject} and ${page}`);
          // console.log(movies1);

          /*  ë§Œì•½ movies1.lengthê°€ 0ì´ë©´ (ë”ì´ìƒ ê°€ì ¸ì˜¬ ë°ì´í„°ê°€ ì—†ìœ¼ë©´)
            í˜ì´ì§€ì—…ê·¸ë ˆì´ë“œ ì•ˆí•˜ê³  ìŠ¤í…Œì´íŠ¸ ì—…ë°ì´íŠ¸ë„ ì•ˆí•˜ê³  ê·¸ëŒ€ë¡œ ë‘”ë‹¤ */
          if (movies1.length > 0) {
            this.setState((current) => ({
              results: movies,
              scroll: false,
              page: current.page + 1,
            }));
          }
        } else if (type === "show") {
          let shows1 = [];
          if (subject === "popular") {
            ({
              data: { results: shows1 },
            } = await tvApi.popular(page));
          }
          if (subject === "topRated") {
            ({
              data: { results: shows1 },
            } = await tvApi.topRated(page));
          }
          if (subject === "airingToday") {
            ({
              data: { results: shows1 },
            } = await tvApi.airingToday(page));
          }
          const movies = [...results, ...shows1];

          this.setState((current) => ({
            results: movies,
            scroll: false,
            page: current.page + 1,
          }));
        }
      }
    } catch {
      this.setState({ error: "Can not find Data from APIzz" });
    } finally {
      this.setState({ loading: false });
    }
  }
  infiniteScroll(e) {
    const { clientHeight, scrollHeight } = document.documentElement;

    console.log(window);
    const { results, scroll } = this.state;
    // if (results && scrollTop + clientHeight >= scrollHeight - 600) {
    if (results && window.scrollY + clientHeight >= scrollHeight - 600) {
      // ã„´ window.scrollY ë¥¼ ì‚¬ìš©í•˜ë©´ ëª¨ë°”ì¼ì—ì„œ ì‘ë™ì´ ë˜ë”ë¼ + IE9 ì´í•˜ì—ì„œëŠ” pageOffsetì„ ì¨ ì¤˜ì•¼í•œë‹¤
      // ã„´ stateì˜ results ê°’ì„ ì²´í¬í•´ì£¼ì§€ ì•Šìœ¼ë©´
      // ã„´ ìŠ¤í¬ë¡¤ì´ ë‚´ë ¤ê°€ìˆëŠ” ìƒíƒœì—ì„œ ìƒˆë¡œê³ ì¹¨ì‹œ getData ê°’ì„ ê°€ì ¸ì˜¤ê¸° ì´ì „ì—
      // ã„´ getDataMoreë¥¼ ì‹¤í–‰í•´ë²„ë¦°ë‹¤ (ê·¸ëŸ¬ë©´ì„œ ì˜¤ë¥˜ê°€ ë°œìƒí•œë‹¤)
      // ã„´
      if (scroll === false) {
        // ã„´ ì´ë ‡ê²Œ ê±¸ëŸ¬ì£¼ì§€ ì•Šìœ¼ë©´ ìŠ¤í¬ë¡¤ì´ ë‚´ë ¤ê°€ì„œ ì¶”ê°€ë¡œë“œí• ë•Œ ë¡œë“œê°€ ì™„ë£Œë ë•Œê¹Œì§€ (falseë¡œ ëŒì•„ì˜¬ë•Œê¹Œì§€)
        // ã„´ ê³„ì† this.setStateë¥¼ ì‹¤í–‰ì‹œì¼œì„œ í•„ìš”ì—†ëŠ” ë Œë”ë§ì„ ëª‡ë²ˆì´ê³  í•œë‹¤
        this.setState({ scroll: true });
        this.getDataMore(this.state.page);
      }
    }
  }
  componentDidMount() {
    console.log("didMount!");
    this.getData();
    window.addEventListener("scroll", this.infiniteScroll.bind(this));
  }
  componentDidUpdate() {}
  render() {
    const {
      match: {
        params: { type, subject },
      },
    } = this.props;
    const { results, loading, error } = this.state;
    console.log("Render");
    //console.log(results);
    return (
      <ViewMorePresenter
        results={results}
        loading={loading}
        error={error}
        isMovie={type === "movie"}
        subject={subject}
      />
    );
  }
}
